// React and Next
import Link from "next/link";
import { Fragment } from "react";

export default function Breadcrumb({ breadCrumb }) {
  return (
    <div className="flex flex-wrap space-x-[5px] px-0 pt-2">
      {breadCrumb.map((data, index) =>
        index + 1 != breadCrumb.length ? (
          <Fragment key={index}>
            <Link
              prefetch={false}
              href={data?.url}
              className={`text-sm font-normal capitalize leading-4 text-[var(--color)] hover:text-green-500 rtl:ml-[5px]`}
            >
              {" "}
              {data.text}{" "}
            </Link>
            <div className="text-sm font-medium leading-4 text-[#333333]">
              {" "}
              /{" "}
            </div>
          </Fragment>
        ) : index != 0 ? (
          <div
            key={index}
            className={`text-sm font-medium capitalize leading-4 text-[#303030]`}
          >
            {" "}
            {data.text}{" "}
          </div>
        ) : (
          <Fragment key={index}>
            <Link
              prefetch={false}
              href={data?.url}
              className={`text-black-600 text-xs capitalize hover:text-green-500`}
            >
              {" "}
              {data.text}{" "}
            </Link>
            <div className="text-black-600 text-xs"> / </div>
          </Fragment>
        ),
      )}
    </div>
  );
}
