// 3rd Party
var currencyFormatter = require("currency-formatter");

// Redux
import { useSelector } from "react-redux";

const CustomCurrencyFormatter = ({ price }) => {
  const currencysymbol = useSelector((state) => state.userReducer.currency);

  // Show '-' only if price is null or undefined or not a valid number
  if (!price || isNaN(price)) {
    return <>-</>;
  }

   const formattedPrice = currencyFormatter?.format(
    Number(price),
    {
      code: currencysymbol?.text,  // Must be "INR" for rupees
      format: currencysymbol?.position === "right" ? "%v %s" : "%s %v",
    },
    { spaceBetweenAmountAndSymbol: true },
    { decimalDigits: 1 }
  );

  return <span>{formattedPrice}</span>
};

export default CustomCurrencyFormatter;
